<template>
  <div class="content" ref="toTop">
    <skeleton v-if="skeletonLoading" :nums="3"></skeleton>
    <div v-else>
      <Header :leftClick="goBack" :leftArrow="!$route.query.browerFirstIn">
        <!-- <div class="tab-bar" slot="title">
          <div :class="{'active': true}">商城订单</div> -->
          <!-- <div :class="{'active': isUplus, 'nobg': !isUplus}">商城订单</div> -->
          <!-- <div @click="doMDOrder">门店订单</div> -->
        <!-- </div>
        <div class="order-header-right" slot="right"> -->
          <!-- <a href="javascript:;" @click="doJfOrder">积分订单</a> -->
          <!-- <span class="icon-List-icSearch" @click="toSearch"></span> -->
          <!-- <a href="javascript:;" @click="toShowMore">更多</a>
        </div> -->
        <div class="order-search" :class="{'order-search-left': $route.query.browerFirstIn}" @click="toSearch" slot="title">
          <div><img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/home/new/search.png">输入商品名、订单号、收货人、手机号搜索订单</div>
        </div>
      </Header>
      <div class="switch">
        <div class="tab">
          <div @click="switchTabChange" :class="status === '0' && 'checked'" id="0" style="width: 0.8rem">全部</div>
          <div @click="switchTabChange" :class="status === '1' && 'checked'" id="1">待付款</div>
          <div @click="switchTabChange" :class="status === '2' && 'checked'" id="2">待发货</div>
          <div @click="switchTabChange" :class="status === '3' && 'checked'" id="3">待收货</div>
          <div @click="switchTabChange" :class="status === '4' && 'checked'" id="4">待评价</div>
          <!-- <div @click="switchTabChange" :class="status === '5' && 'checked'" id="5">售后/维修</div> -->
        </div>
      </div>
      <!-- <div class="mask" v-if="more" @click="more = false" @touchmove.stop.prevent></div>
      <div class="other-list" v-if="more">
        <a v-if="isUplus" href="javascript:;" @click="doHXOrder"><img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/order/huanxin.png"> 焕新订单</a>
        <a href="javascript:;" @click="doJfOrder"><img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/order/jifen.png"> 积分订单</a>
        <a v-if="isUplus" href="javascript:;" @click="doSTOrder"><img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/order/shengtai.png"> 生态权益订单</a>
        <a v-if="isUplus" href="javascript:;" @click="doSTOrder"><img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/order/shengtai.png"> 生态权益订单</a>
      </div> -->

      <!--下啦刷新-->
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <template #pulling><refreshLoading text="下拉刷新"/></template>
        <template #loosing><refreshLoading text="下拉刷新"/></template>
        <template #loading><refreshLoading text="加载中"/></template>
        <template #success><refreshLoading text="加载完成"/></template>
        <!--上啦加载-->
        <van-list
          v-if="list.length > 0"
          v-model="loading"
          :finished="finished"
          :finishedText="''"
          @load="onLoad"
          van-clearfix
          :immediate-check="false"
        >
          <List :parentData = "list" @onRefreshParent = "onRefresh"></List>
        </van-list>
        <div v-if="finished && list.length === 0 && !isLoading" class="no-data">
          <!-- <div :class="{'no-top':true,'noevaluate-icon': status==='4','nodata-icon': status!=='4'}"> </div> -->
          <img class="no-top" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/order/evaluate.png" alt="" v-if="status == 4">
          <img class="no-top" v-else src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/order/nodata.png" alt="">
          <div class="no-bottom"> {{status === '4'?'暂无待评价商品':'暂无订单'}} </div>
        </div>
      </van-pull-refresh>
      <guessYouLike v-if="finished && !loading"></guessYouLike>
    </div>
    <!-- 返回顶部 -->
    <top-feedBack :isShow="showToTopFlag" :code="6" @hiddenFeedBack="hiddenFeedBack" @toTop="fnScrollToTop"></top-feedBack>
  </div>
</template>

<script>
import skeleton from '@/components/skeleton'

// import guessYouLike from '@/components/guessYouLick'
import Header from '@/components/header'
// import refreshLoading from '@/components/refreshLoading'
import { getUcLoginUrl } from '@/http/service/common.js'
import userKeyTarget from '@/utils/userTarget'
import mixins from '@/components/top-feedBack/mixins'

/*
 * 接口配置文件
*/
import personalApi from '@/http/service/index';

/*
 * List列表
*/
import List from './list';

const guessYouLike = () => import('@/components/guessYouLick')
const refreshLoading = () => import('@/components/refreshLoading')

/*
 * 订单列表的
*/
export default {
  name: 'orderListIndex',
  mixins: [mixins],
  components: {
    Header,
    skeleton,
    refreshLoading,
    List,
    guessYouLike,
  },
  data() {
    return {
      /**
       * {status} 订单状态 0/null全部，1：待付款，2：待发货，3：待收货，4：待评价，5：售后维权
       * {pageIndex} 页码
       * {pageSize} 每页条数，大小
       * {keyword} 搜索关键字
       * {orderFlag} 0:全部，1:我的，2:用户
       * {list} 接口返回数据列表
       * {isLoading} 下啦刷新loading
       * {loading} 上啦加载更多的loading
       * {finished} 上啦加载完成
       */
      isUplus: this.$util.isUplus(),
      skeletonLoading: true,
      list: [],
      error: false,
      loading: false,
      finished: false,
      status: '0',
      pageIndex: -1,
      pageSize: 5,
      keyword: '',
      orderFlag: 1,
      isLoading: false,
      more: false, // 更多其他产业订单入口
      showModal: false,
      showExchangeTips: true, // 是否展示年夜饭卡兑换提示
      firstShow: true, // 判断是否第一次进入
    }
  },
  // keep alive前进刷新，后退不刷新
  activated() {
    if (!this.firstShow) {
      if (this.$route.meta.isBack) {
      // 操作，从其他页面进入时，可以操作恢复默认状态、请求接口。。。
      // 页码重置查询
        const context = this;
        const _query = context.$route.query;
        context.status = _query.tagChecked ? _query.tagChecked.toString() : '0';
        // 判断登陆状态，未登录便登陆
        if (context.$util.isHaierApp()) { // 智家
          context.$store.dispatch('handleUpAuth').then((res) => {
            console.log(res); // return true / false
            if (res) {
              // 获取第一页数据
              context.onRefresh();
            } else {
              this.$toast.fail('用户信息异常');
            }
          });
        } else {
          // h5
          // 获取第一页数据
          context.onRefresh();
        }
      }
    } else {
      this.firstShow = false
    }
    this.fnScroll()
  },
  // 判断vue router
  beforeRouteEnter(to, from, next) {
    // 如果是从 mine 或者 payState页面进入，要刷新订单列表，
    if (from.name === 'payState' || from.name === 'success' || from.name === 'b2bPay' || from.name === 'cashier' || from.name === 'mine' || from.name === 'thridEvaluate') {
      to.meta.isBack = true
    } else {
      to.meta.isBack = false
    }
    next()
  },
  created() {
    const _query = this.$route.query;
    this.status = _query.tagChecked ? _query.tagChecked.toString() : '0';
    if (_query.otherOrder) {
      this.orderFlag = 2
    }
  },
  mounted() {
    const context = this;
    if (this.firstShow) {
    // 判断登陆状态，未登录便登陆
      if (context.$util.isHaierApp()) { // 智家
        context.$store.dispatch('handleUpAuth').then((res) => {
          if (res) {
          // 获取第一页数据
            context.onRefresh();
          } else {
            this.$store.dispatch('doLogin')
          }
        });
      } else {
      // h5
      // 获取第一页数据
        context.onRefresh();
      }
    }
    this.hideExchangeTag()
  },
  methods: {
    /**
     * 5s后隐藏年夜饭提示
     */
    hideExchangeTag() {
      setTimeout(() => {
        this.showExchangeTips = false
      }, 5 * 1000);
    },
    goBack() {
      let query = this.$route.query;
      if (query.from === 'h5') {
        this.router.push('/')
      } else {
        this.$store.dispatch('doHistoryBack');
      }
    },
    /**
     * @description 下拉刷新,下啦刷新同时清空现有数据，页码初始化0
     */
    onRefresh() {
      const context = this;
      context.pageIndex = 0;
      // 加载状态结束
      context.loading = false;
      context.finished = true;
      context.isLoading = true;
      /**
        * @description 我的订单列表
      */
      context.getList();
    },

    /**
     * 页面上拉触底事件的处理函数，加载更多
     */
    onLoad() {
      const context = this;
      // context.pageIndex = context.pageIndex + 1;
      /**
       * @description 个人中心 我的订单列表
      */
      context.getList();
    },

    /**
     * @description  个人中心 我的订单列表
     * @param {pageSize} 每页条数大小
     * @param {pageIndex} 第几页
     * @param {status} 订单状态 0/null 全部，1：待付款，2：待发货，3：待收货，4：待评价，5：售后维权
     * @param {keyword} 搜索关键字
     * @param {orderFlag} 0:全部，1:我的，2:用户
     * @param {list} 接口返回数据列表
     * 返回接口请求数据
    */
    getList () {
      const context = this;
      const data = {
        pageIndex: context.pageIndex,
        pageSize: context.pageSize,
        orderStatus: context.status,
        keyword: context.keyword,
        orderFlag: context.orderFlag,
        chId: 60 // 商城固定传60
      }
      this.loading = true
      personalApi.getOrderList(data).then(res => {
        const _resData = res.data;
        // console.log(_resData)
        // 如果是请求的是第一页数据，就清空 list
        this.skeletonLoading = false
        if (context.pageIndex === 0) {
          context.list = [];
        }
        if (_resData.success) {
          // 判断当前是否是 待收货状态 （isReceived是否收到货 true收到， false未收到）
          _resData.data.orders.map((item, index) => {
            item.orderProducts.map((itemChild, indexChild) => {
              item.isReceived = true; // isReceived是否收到货 true收到， false未收到
              if (itemChild.status === 9 || itemChild.status === 10 || itemChild.status === 11 || itemChild.status === 70) {
                item.isReceived = false;
              }
            })
          });
          context.list = context.list.concat(_resData.data.orders);
          context.pageIndex += 1;
          // 加载状态结束
          context.finished = false;
          context.loading = false;
          // 下啦刷新完成
          if (context.isLoading) {
            // this.$toast.success('刷新成功');
            context.isLoading = false;
          }
          // 数据全部加载完成
          if (context.list.length >= _resData.data.countResult) {
            context.finished = true;
          }
        } else {
          this.$toast.fail(_resData.message);
        }
      })
    },

    /**
     * @description  团队tab标签切换内容
     * @param {e} event
     * 返回团队内容切换隐藏
    */
    switchTabChange(e) {
      const _checkedId = e.target.id;
      const context = this;
      context.status = _checkedId;
      context.isLoading = false;
      context.list = []; // 清空list
      /**
       * @description  tab标签切换后，直接刷新数据
       */
      context.onRefresh();
    },

    /**
     * @description 跳转到订单搜索页面
     * @param
     */
    toSearch() {
      const context = this;
      context.$router.push({
        path: `/orderSearch`
      });
    },

    /**
     * 去积分订单
     */
    doJfOrder() {
      getUcLoginUrl({
        redirectUri: 'http://jf.haier.com/m/order/myOrderList.shtml?index=1',
        target: userKeyTarget.JiFenTarget,
        targetTest: userKeyTarget.JiFenTarget_test,
        redirectUriTest: 'https://jftest.haier.com/m/order/myOrderList.shtml',
      }).then(({ data }) => {
        this.more = false
        if (data.success) {
          location.href = data.data
        } else {
          this.$toast(data.message)
        }
      })
    },
    /**
     * 去门店订单
     */
    doMDOrder() {
      this.more = false
      this.$router.push('/storeOrderList');
    },
    /**
     * 去生态权益订单
     */
    doSTOrder() {
      this.more = false
      this.$util.openNewWindow('mpaas://foodEcological?targetUrl=exchangelist', true, true);
    },

    /**
     * 去换新订单
     */
    doHXOrder() {
      this.more = false
      this.$util.openNewWindow('mpaas://homedesign?needAuthLogin=1#/schemeList', true, true);
    },

    // /**
    //  * 显示更多其他产业订单入口
    //  */
    // toShowMore() {
    //   this.more = !this.more
    // },

  },
}
</script>

